export default class Intro {
  constructor() {
    const COURSE_All_INTRO = 'COURSE_TASK_INTRO';
    const COURSE_BASE_INTRO = 'COURSE_BASE_INTRO'; 
    const COURSE_TASK_INTRO = 'COURSE_TASK_INTRO';
    this.initSteps();
    this.init();
  }

  init() {
    if(this.isTaskCreatePage()) {
      this.initTaskCreatePageIntro();
    }
    this.initCourseBaseIntro(this.steps);
  }

  initSteps() {
    this.steps = [
      { 
        intro: "课程管理功能现已全新升级!",
      },
      { 
        element: '#step-1',
        intro: "教学内容的编辑、管理请点击左侧“计划任务”的菜单项进入!",
      },
      { 
        element: '#step-2',
        intro: "在“营销设置”中您可以通过设置决定课程如何销售、如何加入、如何学习!",
      }
    ];
  }

  introStart(steps){
    let intro = introJs();
    intro.setOptions({
      steps: steps,
      skipLabel:'我知道了',
      nextLabel:'下一步',
      prevLabel:'上一步'
    });

    intro.start();
  }

  isTaskCreatePage() {
    return $('#step-4').length;
  }

  initCourseBaseIntro() {
    if (store.get(COURSE_All_INTRO)) {
      //先进入的任务页,做了四部引导
      return false;
    }
    if (!store.get(COURSE_BASE_INTRO)) {
      store.set(COURSE_BASE_INTRO, true);
      return true;
    }
  }

  initTaskCreatePageIntro() {

  }

}







